.card img {
    height: 250px;
    width: 300px;
}

.card {
    width: 100%;
    box-sizing: border-box;
}

.center {
    margin: auto;
}

.onSale {
    font-size: 13px;
    color: red;
    /* color: white; */
    display: inline-block;
    margin-left: 10px;
}

.card-body a {
    text-decoration: none;
    color: black;
}

.card-body a:hover {
    color: rgb(0, 0, 255);
}

.add_cart {
    color: black;
    background-color: rgb(247, 168, 21);
    border: rgb(247, 168, 21);
    border-radius: 2px;
    /* padding: 1px 15px; */
    /* font-weight: bold; */
}

.add_cart:hover {
    color: white;
}

@media screen and (max-width: 1185px) {
    .card img {
        height: 200px;
        width: 250px;
    }
  }

@media screen and (max-width: 975px) {
    .card img {
        height: 150px;
        width: 200px;
    }
}